(function ($, Drupal) {

  Drupal.behaviors.blockquote = {
    attach: function(context, settings) {

      'use strict';

      $(context).find('blockquote').each(function() {

        var pq_icon = '<div class="pull-quote__icon"><i class="fas fa-quote-left fa-3x"></i></div>';

        if ($(this).hasClass('pull-quote')) {
          $('.pull-quote p').wrap('<div class="pull-quote__text"></div>');
          $('.pull-quote__text').before(pq_icon);
        }

      });

    }
  }

})(jQuery, Drupal);
